body {
  box-sizing: border-box;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.people {
  grid-template-columns: 1fr 1fr;
  justify-self: center;
  gap: 30px;
  margin: 30px 35px;
  display: grid;
}

.person {
  text-align: center;
  width: 200px;
  position: relative;
}

.person__inner {
  transform-origin: center;
  flex-direction: column;
  align-items: center;
  padding: 0;
  transition: background-color .3s, border-radius .3s, padding .3s;
  display: flex;
}

.person__inner:hover {
  z-index: 10;
  background-color: #eee;
  border-radius: 12px;
  align-items: center;
  width: 220px;
  transform: scale(1.1);
}

.person__photo--wrapper {
  width: 200px;
  position: relative;
}

.person__photo {
  border-radius: 50%;
  width: 200px;
  transition: border-radius 1s;
}

.person__photo:hover {
  border-radius: 60px;
}

.person__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  padding: 7px 12px;
  font-size: 16px;
  font-weight: 700;
  position: absolute;
  top: 84px;
  right: 10px;
}

.person__name {
  color: #2f4f4f;
  font-size: 20px;
  font-weight: 700;
}

.person__details {
  width: 200px;
}

@media (width >= 750px) {
  .person {
    justify-self: center;
  }

  .people {
    margin: 30px 160px;
  }

  .person {
    width: 200px;
  }
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    justify-self: center;
    margin: 30px 305px;
    display: grid;
  }

  .person {
    width: 200px;
  }
}
